@import '../../../styles/variables.scss';

.input {
  width: 255px;
  height: 30px;
  border: none;
  border-radius: 5px;
  padding: 0 0 0 20px;
}

@media (max-width: 900px) {
  .input {
    margin-top: 20px;
  }
}

@media (max-width: 360px) {
  .input {
    width: 200px;
  }
}
